import Vue from 'vue';
import Router from 'vue-router';
import Home from '../components/Home';
// import Content from '../components/Content';

Vue.use(Router);

const routes = [
  {
    path: '/',
    component: Home,
    props: true
  }
];



export default new Router({
  mode: 'history',
  base: '/',
  fallback: false,
  routes
});