<template>
  <div class="home">
    <Header />
    <HomeCarousel />
    <div class="home-Content" v-html="homeContent.html_context" />
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Vue from 'vue'
import axios from 'axios'
import Header from './Header'
import HomeCarousel from './homeCarousel'

export default {
  name: 'Home',
  components: {
    Header,
    HomeCarousel
  },
  computed: {
    ...mapGetters([
      'homeContext',
      'headerNav',
      'homeContent'
    ])
  },
  created() {
    // this would make post into options
    // axios.defaults.headers.post['Content-Type'] = 'application/json';
    // axios.defaults.headers.get['Cache-Control'] = 'max-age=3600';

    axios.get("https://sniweb.shouting.feedia.co/php/GetHomePage.php", {
      'Cache-Control': 'max-age=3600'
    }).then(((res) => {
      this.actionUpdateContext(res.data);
    }));

    axios.get("https://sniweb.shouting.feedia.co/php/GetNav.php").then(((res) => {
      this.actionHeaderNav(res.data);
    }));

    axios.get("https://sniweb.shouting.feedia.co/php/GetContent.php", {
      params: {
        "page_id": Vue.cookies.get('pageId')
      }
    }).then((response) => {
      this.actionSetContent(response.data);
    });
  },
  methods: {
    ...mapActions([
      'actionUpdateContext',
      'actionHeaderNav',
      'actionSetContent'
    ])
    // login(account, password) {
    //   axios.get("https://sniweb.shouting.feedia.co/php/login.php", {
    //     account,
    //     password
    //   }).then(((res) => {
    //     console.log('it worked', res);
    //   }));
    // }
  }
}
</script>

