<template>
    <div class="header-wrap">
        <div class="header-title">
            <h1>{{ homeContext.web_title }}</h1>
            <p>{{ homeContext.web_sub_title }}</p>
        </div>
        <Dropdown class="is-mobile home-menu" trigger="click">
            <a href="javascript:void(0)">
                <Icon type="md-menu" size="25"/>
            </a>
            <DropdownMenu slot="list">
                <Dropdown
                    v-for="(item, index) in headerNav"
                    :key="`menu-group-${index}`"
                    :title="item.group_name"
                    placement="left"
                >
                    <DropdownItem>
                        <Icon type="md-arrow-back" />
                        {{ item.group_name }}
                    </DropdownItem>
                    <DropdownMenu
                        v-for="(subItem, index) in item.page_content"
                        :key="`subItem-${index}`"
                        :name="`menu-item-${subItem.page_name}`"
                        @click.native="enterPage(subItem.page_content_id)"
                        slot="list"
                    >
                        <DropdownItem>{{ subItem.page_name }}</DropdownItem>
                    </DropdownMenu>
                </Dropdown>
            </DropdownMenu>
        </Dropdown>
        <Menu class="is-pc" mode="horizontal" :theme="theme1" active-name="1">
            <Submenu
                v-for="(item, index) in headerNav"
                :key="`menu-${index}`"
                :name="item.page_group_id"
            >
                <template slot="title">
                    <Icon type="ios-stats" />
                    {{ item.group_name }}
                </template>
                <MenuItem
                    v-for="(subItem, subIndex) in item.page_content"
                    :key="`submenu-${subIndex}`"
                    :name="`${index}-${subIndex}`"
                    @click.native="enterPage(subItem.page_content_id)"
                >
                    {{ subItem.page_name || 'test' }}
                </MenuItem>
            </Submenu>
        </Menu>
    </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
import Vue from 'vue'
import axios from 'axios'


export default {
    data() {
        return {
            theme1: 'light'
        };
    },
    computed: {
        ...mapGetters([
            'homeContext',
            'headerNav'
        ])
    },
    methods: {
        ...mapActions([
        'actionUpdateContext',
        'actionHeaderNav',
        'actionSetContent'
        ]),
        enterPage(id) {
            axios.get("https://sniweb.shouting.feedia.co/php/GetContent.php", {
                params: {
                    "page_id": id
                }
            }).then((response) => {
                this.actionSetContent(response.data);
                Vue.cookies.set('pageId', id);
            });
        }
    }
}
</script>

